.quit {
  font-size: 5vh;
  position: absolute;
  top: 5vh;
  right: 4vw;
  -webkit-transition: all 1s ease-in;
  transition: all 1s ease-in;
}

.quit:hover {
  color: #DAC1A6;
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

.opening {
  -webkit-animation: opening 1s 0s ease-in forwards;
          animation: opening 1s 0s ease-in forwards;
}

.closing {
  -webkit-animation: closing 1s 0s ease-in forwards;
          animation: closing 1s 0s ease-in forwards;
}

@-webkit-keyframes opening {
  0% {
    -webkit-transform: perspective(100vh) rotateX(0deg);
            transform: perspective(100vh) rotateX(0deg);
  }
  100% {
    -webkit-transform: perspective(100vh) rotateX(-30deg);
            transform: perspective(100vh) rotateX(-30deg);
  }
}

@keyframes opening {
  0% {
    -webkit-transform: perspective(100vh) rotateX(0deg);
            transform: perspective(100vh) rotateX(0deg);
  }
  100% {
    -webkit-transform: perspective(100vh) rotateX(-30deg);
            transform: perspective(100vh) rotateX(-30deg);
  }
}

@-webkit-keyframes closing {
  0% {
    -webkit-transform: perspective(100vh) rotateX(-30deg);
            transform: perspective(100vh) rotateX(-30deg);
  }
  100% {
    -webkit-transform: perspective(100vh) rotateX(0deg);
            transform: perspective(100vh) rotateX(0deg);
  }
}

@keyframes closing {
  0% {
    -webkit-transform: perspective(100vh) rotateX(-30deg);
            transform: perspective(100vh) rotateX(-30deg);
  }
  100% {
    -webkit-transform: perspective(100vh) rotateX(0deg);
            transform: perspective(100vh) rotateX(0deg);
  }
}

