  -webkit-animation: up 0.5s 0.5s ease-in-out forwards;
          animation: up 0.5s 0.5s ease-in-out forwards;
}

.down {
  -webkit-animation: down 0.5s 0s ease-out forwards;
          animation: down 0.5s 0s ease-out forwards;
}

@-webkit-keyframes up {
  0% {
    top: 100%;
  }
  100% {
